import axios from 'axios'
import configService from './config'

const FileExchange = {}

  FileExchange.getFileFromStorage = (fileName,path) => {
    return new Promise(function(resolve, reject) {
      axios.get(configService.apiUrl + '/getFileFromStorage', {
        params: {
          fileName: fileName,
          path: path
        }
      })
      .then(function(res) {
        resolve(res)
      })
      .catch(function(err) {
        reject(err)
      })
    })
  }
  FileExchange.uploadToStorage = (file,path) => {
    let formData = new FormData();
    formData.append('archivo', file);
    formData.append('path', path);
    return new Promise(function(resolve, reject) {
      axios.post(configService.apiUrl + '/uploadFileToStorage', formData, {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      })
      .then(function(res) {
        resolve(res)
      })
      .catch(function(err) {
        reject(err)
      })
    })
  }

  export default FileExchange